.search{
    padding: 12px;
    background-image: linear-gradient(90deg,#4cafff,#2e93e6);
    position: sticky;
    top: 0;
    z-index: 2;
}
.searchCenter{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    max-width: 1110px;
    margin: 0 auto;
    position: relative;
}
.searchBox{
    width: 100%;
    padding: 15px 40px 15px 15px;
    font-size: 16px;
    font-weight: 600;
    outline: none;
    border: transparent;
    border-radius: 3px;
    letter-spacing: 0.6px;
    transition: 1s all;
}
::placeholder{
    color: rgb(167, 166, 166);
}
.searchIcon{
    font-size: 17px;
    position: absolute;
    right: 18px;
    cursor: pointer;
}

.logo1{
    font-size: 0px;
    color: white;
    cursor: pointer;
    transform: scaleX(1.2);
    transition: 0.5s all;
}
.searchBox:not(:placeholder-shown) + .logo1{
    font-size: 30px;
    margin:0 12px 0px 0;
}