.header{
    padding: 0px 12px;
}
.headerCenter{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 1110px;
    margin: 0 auto;
}
.logo p{
    font-size: 34px;
    color: #007ADD;
    cursor: pointer;
}
.menu button{
    outline: none;
    cursor: pointer;
    font-weight: bold;
}
.create,.signin{
    padding: 7px 15px;
    font-size: 13px;
    background-color: #007ADD;
    color: white;
    border: 1px solid transparent;
    border-radius: 3px;
    margin: 9px 10px;
    transition: 0.2s all;
}
.signin{
    background-color: white;
    color: #007ADD;
    border: 1px solid ;
}
.create:hover,.signin:hover{
    transform: translateY(-1.5px);
    box-shadow: 0 0 5px rgba(128, 128, 128, 0.688);
}
.hamburger{
    margin-left: 10px;
    font-size: 25px;
    transform: scaleX(1.3);
    cursor: pointer;
}
.menu{
    display: flex;
    align-items: center;
}
.upload{
    padding: 0 5px 0 0;
    font-size: 15px;
}