 .trending{
  padding: 0 12px;
  position: relative;
  }
  .trendingList{
    max-width: 1110px;
    margin: 20px auto;
    overflow: hidden;
  }
  .trendingHead{
    margin: 10px 0px;
    font-size: 26px;
  }
  .trendingGifs{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .trendingImg{
    display: flex;
    transition: 0.5s all;
  }
  .trendingImgList{
    margin: 0 9px;
  }
  .trendingImgSize{
    background-position: 50%;
    background-size: cover;
    border-radius: 5px;
    height: 90px;
    width: 206px;
    transition: 0.1s all;
  }
  .trendingImgSize:hover{
    transform: translateY(-2px);
  }
  .trendingImgList{
    overflow: hidden;
    position: relative;
    cursor: pointer;
    padding-top: 10px;
  }
 .trendingNames{
  width: 206px;
  font-size: 16px;
  font-weight: 500;
  padding: 5px 0;
 }
  .trendingCarousel{
    position: absolute;
    font-weight: bold;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    width: 1155px;
    margin: 0 -23px;
  }
  .left,.right{
    cursor: pointer;
    color: gray;
    transition: 0.1s all;
  }
  .left:hover,.right:hover{
    color: black;
  }
  .left{
    visibility: hidden;
  }

  @media only screen and (max-width: 1200px) {
    .trendingList{
      max-width: 800px;
    }
    .trendingCarousel{
      width: 845px;
    }
  }
  @media only screen and (max-width: 880px) {
    .trendingList{
      max-width: 600px;
    }
    .trendingCarousel{
      width: 645px;
    }
    .right{
      display: none;
    }
    .trendingImg{
      scroll-snap-type: x mandatory;
      scroll-padding: 50%;
      overflow-x: auto;
    }
    .trendingImgList {
      scroll-snap-align: start;
      overflow:visible;
    }
  }
  @media only screen and (max-width: 700px) {
    .trendingList{
      max-width: 400px;
    }
    .trendingCarousel{
      width: 445px;
    }
  }
  @media only screen and (max-width: 500px) {
    .trendingList{
      max-width: 320px;
    }
    .trendingCarousel{
      width: 365px;
    }
  }