.featured{
    padding: 0 12px;
  }
  .featuredList{
    max-width: 1110px;
    margin: 20px auto;
  }
  .featuredHead{
    margin: 20px 0;
    font-size: 26px;
  }
  .featuredImg{
    display: flex;
    gap: 18px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .featuredImg img{
    width: 263px;
    border-radius: 5px;
    box-shadow: 0 0 5px gray;
    transition: 0.2s all;
    cursor: pointer;
  }
  .featuredImg img:hover{
    transform: scale(1.01);
  }
  .columns{
    display: flex;
    flex-direction: column;
    gap: 20px;
  }